<template>
  <div class="pay">
    <div class="top"> 
      <div>
        <img style="border-radius:50%;" :src="$utils.dealImg(detail.userHeader)" alt />
        <p class="text">向{{detail.userName}}转出</p>
      </div>
    </div>
    <div class="mid">
      <div class="item flex line">
        <p class="text">您的应付款余额:</p>
        <p class="price">{{acAmt}}</p>
      </div>
      <div class="item line">
        <p class="text">本次支付总额</p>
        <div class="flex marginTop">
          <p class="font">￥</p>
          <p>
            <input
              type="number"
              :style="balance?'color:#333;':''"
              v-model="balance"
              placeholder="0.00"
              @blur="blur"
            />
          </p>
        </div>
      </div>
      <div class="item line"> 
        <div class="flex marginTop"> 
          <p>
            <input class="checkcode" type="number" v-model="checkcode" placeholder="请输入验证码"  @blur="blur" />
          </p> 
            <div class="btn2" @click="submitsendcheckcode">获取验证码<span v-if="min">({{min}})</span></div> 
        </div>
      </div>
      <div class="item flex line" >
        <van-checkbox v-model="checked" style="margin:0 5px 0 0" />
        <p class="text flex-2">应付款余额支付</p>
        <p class="price">{{acAmt}}</p>
      </div>
      <div class="btn" @click="submit">确认支付</div>
    </div>
  </div>
</template>
<script>
import { Checkbox } from "vant";
import {  getPersonInfo, personPay2 ,sendcheckcode } from "@/api";
export default {
  components: {
    [Checkbox.name]: Checkbox
  },
  data() {
    return {
      balance: "",
      detail: {},
      acAmt: 0,
      checked: true,
      checkcode: "",
      min: 0
    };
  },
  methods: {
    blur() {
      document.body.scrollTop = 0;
    },
    submitsendcheckcode(){
        if (this.min) {
          return;
        }
        this.$toast.allowMultiple();
        const loading = this.$toast.loading({
          duration: 0, // 持续展示 toast
          forbidClick: true // 禁用背景点击
        });
        
        sendcheckcode().then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$toast("验证码发送成功，请在您的手机短信中查看验证码");
            this.min = 60;
            this.Interval = setInterval(() => {
              this.min--;
              if (this.min < 1) {
                clearInterval(this.Interval);
              }
            }, 1000); 
          }
          else{
            this.$toast(res.msg);
          }
        });
    },
    submit() {
      if (!this.balance) {
        this.$toast("请输入应付款金额");
        return;
      }
      if (this.balance > this.acAmt) {
        this.$toast("余额不足");
        return;
      }
      if (!this.checkcode) {
        this.$toast("请输入短信验证码");
        return;
      }
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      }); 
      personPay2({
          userCode: this.$route.query.userCode,
          moneyNum: parseInt(this.balance * 100),
          checkcode: this.checkcode
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.$router.push({
              name: "payDetail",
              query: {
                balance: this.balance,
                result: res.data.return ? "1" : "2",
                userName: this.detail.userName,
                type: "2"
              }
            });
          } else {
            this.$toast(res.msg);
          }
        });
      
    },
    weChatPay(appId, timeStamp, nonceStr, packageValue, signType, sign) {
      var that = this;
      if (typeof WeixinJSBridge == "undefined") {
        if (document.addEventListener) {
          document.addEventListener(
            "WeixinJSBridgeReady",
            onBridgeReady,
            false
          );
        } else if (document.attachEvent) {
          document.attachEvent("WeixinJSBridgeReady", onBridgeReady);
          document.attachEvent("onWeixinJSBridgeReady", onBridgeReady);
        }
      } else {
        onBridgeReady();
      }
      function onBridgeReady() {
        WeixinJSBridge.invoke(
          "getBrandWCPayRequest",
          {
            appId: appId, //公众号名称，由商户传入
            timeStamp: timeStamp, //时间戳，自1970年以来的秒数
            nonceStr: nonceStr, //随机串
            package: packageValue,
            signType: signType, //微信签名方式：
            paySign: sign //微信签名
          },
          function(res) {
            console.log(res);
            if (res.err_msg == "get_brand_wcpay_request:ok") {
              that.$toast("支付成功", "middle");
              setTimeout(() => {
                that.$router.push({
                  name: "payDetail",
                  query: {
                    balance: that.balance,
                    result: "1",
                    shopName: that.detail.shopName,
                    type: "1"
                  }
                });
              }, 500);
            } else if (res.err_msg == "get_brand_wcpay_request:cancel") {
              that.$toast("支付取消", "middle");
            } else if (res.err_msg == "get_brand_wcpay_request:fail") {
              that.$toast("支付失败，请稍后再试", "middle");
            }
            // 使用以上方式判断前端返回,微信团队郑重提示：res.err_msg将在用户支付成功后返回    ok，但并不保证它绝对可靠。
          }
        );
      }
    },
    loadData() {
      const loading = this.$toast.loading({
        duration: 0, // 持续展示 toast
        forbidClick: true // 禁用背景点击
      });
      if (this.$route.query.type == "1") {
        getBusinessInfo({
          shopCode: this.$route.query.shopCode
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.detail = res.data;
            this.acAmt = res.data.myAccount[0].acAmt;
          } else {
            this.$toast(res.msg);
          }
        });
      } else {
        getPersonInfo({
          userCode: this.$route.query.userCode
        }).then(res => {
          loading.clear();
          if (res.code == 0) {
            this.detail = res.data;
            this.acAmt = res.data.myAccount[0].acAmt;
          } else {
            this.$toast(res.msg);
          }
        });
      }
    }
  },
  mounted() {
    document.title = "付款";
    this.loadData();
  }
};
</script>
<style lang="scss" scoped>
.pay {
  background: #eee;
  height: 100vh;
  .top {
    text-align: center;
    background: #fff;
    padding: 20px 0;
    img {
      width: 100px;
    }
    .text {
      line-height: 30px;
      font-size: 17px;
      color: #999;
      margin: 5px 0 0;
    }
  }
  .mid {
    background: #fff;
    margin: 20px 0 0;
    padding: 0 10px 20px;
    .flex {
      display: flex;
      align-self: center;
      .flex-2 {
        flex: 2;
      }
    }
    .line {
      border-bottom: 1pt solid #eee;
    }
    .item {
      padding: 20px 0;
      line-height: 25px;
      .text {
        color: #333;
        font-weight: bold;
        margin-right: 5px;
      }
      .price {
        font-size: 25px;
        color: #ef5c54;
      }
      .marginTop {
        margin-top: 5px;
        padding-bottom: 5px;
      }
      .font {
        margin-right: 5px;
        font-size: 30px;
        line-height: 30px;
        color: #ef5c54;
      }
      input {
        border: 0;
        width: 100%;
        height: 30px;
        font-size: 30px;
        line-height: 30px;
        color: #999;
      }
    }
    .btn {
      background: #ef5c54;
      color: #fff;
      text-align: center;
      height: 50px;
      line-height: 50px;
      font-size: 18px;
      border-radius: 5px;
      margin: 10px 0 0;
    }
    input.checkcode{
        border: 0;
        width: 100%;
        height: 20px;
        font-size: 20px;
        line-height: 20px;
        color: #999;
    }
    .btn2 {
      width: 100px;
      display:inline-block;
      background: #ef5c54;
      color: #fff;
      text-align: center;
      height: 30px;
      line-height: 30px;
      font-size: 14px;
      border-radius: 5px;
      margin: 10px 0 0; 
    }
  }
}
</style>